/* ========================================================================
   #TYPOGRAPHY
   ======================================================================== */

:root {

    /**
     * Mor readable bodytext
     * see http://practice.typekit.com/lesson/caring-about-opentype-features/
     */
    --font-bodytext: {
        font-variant-ligatures: common-ligatures, contextual;
        font-variant-numeric: oldstyle-nums, proportional-nums;
        font-feature-settings: "kern", "liga", "clig", "calt", "onum", "pnum";
        font-kerning: normal;
    };





    /**
     * Mor readable headings
     * see http://practice.typekit.com/lesson/caring-about-opentype-features/
     */
    --font-headings: {
        font-variant-ligatures: common-ligatures, contextual,
            discretionary-ligatures;
        font-feature-settings: "kern", "liga", "clig", "calt", "dlig";
        font-kerning: normal;
    };





    /**
     * More readable sub text
     * see http://practice.typekit.com/lesson/caring-about-opentype-features/
     */
    --font-sub: {
        font-variant-ligatures: common-ligatures, contextual;
        font-variant-position: sub;
        font-feature-settings: "kern", "liga", "clig", "calt", "subs";
        font-kerning: normal;
    };





    /**
     * OpenType feature for small caps
     * see http://practice.typekit.com/lesson/caring-about-opentype-features/
     *
     * 1. Safari iOs doesn't support `font-feature` so we use `text-transform`
     *    to make the texte uppercase. http://caniuse.com/#search=font-feature
     */
    --font-small-caps: {
        font-variant-ligatures: common-ligatures, contextual;
        font-variant-numeric: oldstyle-nums, proportional-nums;
        font-variant-caps: all-small-caps;
        font-feature-settings: "kern", "liga", "clig", "calt", "onum", "pnum",
            "smcp", "c2sc";
        font-kerning: normal;
        text-transform: uppercase; /* [1] */
    };





    /**
     * OpenType feature for data
     * see http://practice.typekit.com/lesson/caring-about-opentype-features/
     */
    --font-data: {
        font-variant-ligatures: common-ligatures, contextual;
        font-variant-numeric: lining-nums, tabular-nums;
        font-feature-settings: "kern", "liga", "clig", "calt", "lnum", "tnum";
        font-kerning: normal;
    };
}
